import React from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'
import PlaylistContent from '../playlist/PlaylistContent'
import SearchContent from '../search/SearchContent'
import HomeContent from '../home/HomeContent'
import MediaCollectionContent from '../mediaCollection/MediaCollectionContent'

const ContentSwitcher = () => {
  return (
    <Switch>
      <Route path="/lissnight/collection">
        <MediaCollectionContent />
      </Route>
      <Route path="/lissnight/playlist/:id">
        <PlaylistContent />
      </Route>
      <Route path="/lissnight/search/:q">
        <SearchContent />
      </Route>
      <Route path="/lissnight">
        <HomeContent />
      </Route>
      <Route path="/">
        <Redirect to={'/lissnight'} />
      </Route>
    </Switch>
  )
}

export default ContentSwitcher
